              {{this.$store.state.city}}
              <span class="iconfont arrow-icon">&#xe64a;</span>
          </div>
        </router-link>
    </div>
</template>

<script>
export default {
  name: 'HomeHeader'
}
</script>

<style lang="stylus" scoped>
    @import '~styles/varibles.styl'
    .header
      display: flex
      line-height: $headerHeight
      background: $bgColor
      color: #fff
      .header-left
        width:.64rem
        float: left
        .back-icon
          text-align: center
          font-size: .4rem
      .header-input
        flex:1
        height: .64rem
        line-height: .64rem
        margin-top: .12rem
        margin-left: .2rem
        margin-right: .3rem
        padding-left: .2rem
        background: #fff
        border-redius: .1rem
        color: #ccc
      .header-right
        width:1.24rem
        float: right
        text-align: center
        color: #fff
        .arrow-icon
          text-align: center
          font-size: .4rem
          margin-left: -.04rem
          font-size: .24rem
</style>



